.appRoot{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.3);
  z-index: 101;
}

.modal {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-image: linear-gradient(to top left, rgba(0,90,167,0.6) 0%, rgba(255,253,228,0.7) 100%);
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  border-radius: 15px 15px 0 0;
  z-index: 102;
}
.modal-wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

#tsparticles{
  width: 100%;
  height: 100%;
  margin-bottom: 150px;
  position: absolute;
}

.experience {
  padding-top: 35px;
}

#contact::after{
  content: "";
  background: linear-gradient(to bottom, 
                            rgba(0,0,0,0.9) 0%, 
                            rgba(0,36,36,0.65) 30%, 
                            rgba(11,64,56,0.75) 50%, 
                            rgba(0,88,89,0.6) 70%, 
                            rgba(255,255,255,0.9) 100%), 
                            url('./images/workstation.jpg');
  mix-blend-mode: difference;
  background-size: cover;
  background-position-x: center;
  background-repeat: no-repeat;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  z-index: -1;
  height: 100%;
}


@media only screen and (max-width: 959px){
  #root > div > div.experience > div:nth-child(2) > div{
    background-attachment: initial;
  }

  #root > div > div.experience > div:nth-child(3) > div{
    background-image:linear-gradient(to top left, rgba(0,0,0,0.3) 0%,rgba(0,0,0,0.5) 100%), url('./images/geisel-library.jpg') !important;
    background-attachment: initial;
  }

  #root > div > div.experience > div:nth-child(4) > div{
    background-image:linear-gradient(to top left, rgba(0,0,0,0.3) 0%,rgba(0,0,0,0.5) 100%), url('./images/artisan.jpg') !important;
    background-attachment: initial;
  }
}

